<template>
    <div>
        <div class="loading" v-if="!isShow">
            <img src="../../assets/img/timg.gif" alt="">
        </div>
        <div v-else>
            <a-player ref="aplayer"  :audio="audio" :lrc-type="0"></a-player>
            <table class="music-list">
                <thead>
                    <tr>
                        <td></td>
                        <td>歌曲</td>
                        <td>歌手</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in dataList" @click="clickHandle(item.id)" :key="item.id">
                        <td>{{index+1}}</td>
                        <td>{{item.name}}</td>
                        <td>{{item.ar[0].name}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>



<script>
    import Vue from "vue";
    import APlayer from "@moefe/vue-aplayer";   
    Vue.use(APlayer, {
        defaultCover: "https://github.com/u3u.png",
        productionTip: true
    });
    export default {
        data() {
            return {
                dataList: [],
                isShow: false,
                audio: [{
                    name: "东西（Cover：林俊呈）",
                    artist: "纳豆",
                    url: "https://cdn.moefe.org/music/mp3/thing.mp3",
                    cover: 'https://p1.music.126.net/5zs7IvmLv7KahY3BFzUmrg==/109951163635241613.jpg?param=300y300'
                }],
            };
        },
        created() {
            let obj = {
                title: "音乐",
                navName: "music"
            };
            this.$emit("changeActive", obj);
            this.getData();
        },
        methods: {
            clickHandle(id) {
                let thisUrl = "https://bird.ioliu.cn/netease/song?id=" + id;
                axios.get(thisUrl).then((res) => {
                    let objs = {
                        name: res.data.data.name,
                        artist: res.data.data.ar[0].name,
                        url: res.data.data.mp3.url,
                        cover: res.data.data.al.picUrl
                    }
                    Vue.set(this.audio, 0, objs)
                    // console.log(obj)
                    // this.audio        = obj;
                    // this.audio[0].name   = res.data.data.name;
                    // this.audio[0].artist = res.data.data.ar[0].name;
                    // this.audio[0].url    = res.data.data.mp3.url;
                    // this.audio[0].cover  = res.data.data.al.picUrl
                    // this.$refs.aplayer.play();
                })
            },
            getData() {
                let thisUrl = "https://bird.ioliu.cn/netease/playlist?id=2899785819";
                axios.get(thisUrl).then(res => {
                    this.dataList = res.data.playlist.tracks;
                    this.isShow = true;
                });
            }
        }
    };
</script>
<style scoped>
    .music-list {
        font-size: 0.4rem;
        border: 0;
        border-collapse: collapse;
    }

    .music-list tbody tr:nth-child(odd) {
        background-color: red;
    }

    .loading {
        width: 100vw;
    }

    .loading img {
        width: 100%;
    }
</style>
